#header {
    margin-left: 15%;
    margin-top: 0%;
}

#intro1 {
    
    font-weight: 700;
    color: rgba(255, 255, 255, 0.90);
    font-size: 3.8em;
    display: inline-block;
    vertical-align: top;
    margin-top: 5%;
    margin-left: -2%;
}

#title,
#semititle {
    
    font-weight: 700;
}

#title {
    font-size: 1.875em;
}

#semititle {
    font-size: 1.125em;
}

#aboutcontext{
    margin-top: 0.4em;
    font-size: 1.2em;
}
#semicontext {
    margin-top: 0.4em;
    font-size: 1em;
}

#introcontext {
    
    font-weight: 500;
    padding-top: 1.0625em;
    line-height: 1.2em;
    font-size: 1.855em;
}

#introduce,
#about {
    position: absolute;
    display: inline-block;
   
    margin-top: -4%;
    background-color: rgba(255, 255, 255, 0.55);
    box-shadow: 0px 16px 40px rgba(112, 144, 176, 0.15);
    border-radius: 50px;
    
    box-sizing: border-box;
    padding: 1.875em 3.125em;
}

#introduce {
    margin-left: 12%;
    width: 42.5%;
}

#about {
    margin-left: 58%;
    width: 32.5%;
}

#education {
    padding-top: 0.625em;
}

#email {
    padding-top: 1.25em;
    
    font-size: 0.9375em;
    font-weight: 700;
}

#copy {
    margin-left: 5%;
    padding: 0.25em 0.5em;
    background: #f0efef;
    box-shadow: 4px 16px 40px rgba(0, 0, 0, 0.15);
    border-radius: 50px;
    border: transparent;
}
