
#document{
 
    margin-top: 3%;
    width: 80%;
    max-height: 90%;
  
    overflow-y: auto; 
    font-size: 1.5em;
    font-family: "Pretendard";
    color: #000000;
    background-color:  rgb(255, 255, 255, 0.75);
    border-bottom-left-radius: 30px;
    
  
    margin-left: 10%;
    padding-left: 1em;
  }
  #title1{
    margin-left: 42%;
    margin-top: 3%;
    margin-bottom: 3%;
    font-size: 2em;
  }
  #name{
    font-size: 3em;
    font-weight: 700;
  }
  #job{
    font-size: 1.5em;
    font-weight: 600;
  }
  #detail-info{
    float: right;
    margin-right: 1em;
    margin-top: 1em;
    font-size: 0.8em;
    border-collapse: collapse;
  }
  #middle-title{
    font-size: 1.5em;
    font-weight: 600;
  }
  table{
    text-align: center;
    border: 0.05em solid rgb(0, 0, 0);
    border-collapse: collapse;
  }
  
  #pjtable{
    width: 95%;
  }
  #sktable{
    width: 60%;
  }
  
  th,td{
    padding: 0.15em;
    border-bottom: 0.05em solid rgb(0, 0, 0);
    border-right: 0.05em solid rgb(0, 0, 0);
  }
  
  ul{
    list-style-image: url("data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cline x1='5' y1='11.25' x2='19' y2='11.25' stroke='black' stroke-width='1.5'/%3E%3C/svg%3E%0A");
  }
  
  #pjexplain{
    font-size: 1.125em;
    font-weight: 600;
  }
  
  #homebtn{
    position: absolute;
    margin-top: 3%;
    top:0;
    background-color: rgba(255, 255, 255, 0.75);
    box-shadow: 0.0625em 0em 2.5em rgba(112, 144, 176, 0.15);
    border-top-left-radius: 30px;
    border-bottom-left-radius: 30px;
  
    margin-left: 6.1%;
    padding: 2.45em 1em 2em 1em;
    transition: width 250ms;
  
  }
  
  #homebtn:hover {
  
    background-color: #685bfb;
    box-shadow: none;
  }