#introbox {
    position: relative;
    margin-left: 12%;
    margin-top: 4%;
}

#intro, #intro2, #intro3 {
    
    font-weight: 700;
}

#intro {
    color: rgba(255, 255, 255, 0.90);
    font-size: 8em;
    vertical-align: top;
}

#intro2 {
    color: rgba(255, 255, 255, 0.9);
    font-size: 3em;
}

#intro3 {
    color: rgba(255, 255, 255, 0.75);
    font-size: 1.8em;
    text-decoration: underline;
}

#project-lang, #contest-grade {
    
    font-size: 1.125em;
    font-weight: 600;
    color: #FFFFFF;
    text-align: center;
    padding: 0.3125em;
    width: 5.3125em;
    margin-left: 9.5%;
    background: linear-gradient(87deg, rgb(80, 65, 255), rgb(164, 156, 255));
    box-shadow: 0px 16px 40px 20px rgba(112, 144, 176, 0.15);
    border-radius: 0px 0px 20px 20px;
}

#block-title {
    justify-content: center;
    display: flex;
    text-align: center;
    
    font-size: 1.375em;
    font-weight: 600;
}

#project-frame, #contest-frame {
    position: absolute;
    display: inline-block;
    width: 74%;
    height: 100%;
    margin-top: -5%;
    margin-left: 20%;
    background-color: rgba(255, 255, 255, 0.75);
    box-shadow: 0px 16px 40px rgba(112, 144, 176, 0.15);
    border-radius: 50px;
}

#block-img {
    position: relative;
    width: 90%;
    height: 70%;
    margin-top: 5%;
    margin-left: 5%;
    border-radius: 50px;
}

#detail {
    margin-top: 3%;
    width: 80%;
    max-height: 90%;
    overflow-y: auto;
    font-size: 1.5em;
    font-family: "Pretendard";
    color: #000000;
    background-color: rgb(255, 255, 255, 0.75);
    border-bottom-left-radius: 30px;
    margin-left: 10%;
    padding-left: 1em;
}

#projectdetail-img {
    margin-left: 10%;
    width: 80%;
    max-height: 90%;
}

#returnbtn {
    position: absolute;
    margin-top: 3%;
    top: 0;
    background-color: rgba(255, 255, 255, 0.75);
    box-shadow: 0.0625em 0em 2.5em rgba(112, 144, 176, 0.15);
    border-top-left-radius: 30px;
    border-bottom-left-radius: 30px;
    margin-left: 6.1%;
    padding: 2.45em 1em 2em 1em;
    transition: width 250ms;
}

#returnbtn:hover {
    background-color: #685bfb;
    box-shadow: none;
}
