#skill-title, #skill-semititle {
    
    font-weight: 700;
}

#skill-title {
    font-size: 1.875em;
}

#skill-semititle {
    font-size: 1.125em;
}

#skill-context,
#skill-semicontext{
    padding-top: 0.625em;
    vertical-align: super;
}

#devskill,
#etcskill,
#softskill {
    position: absolute;
    display: inline-block;
    width: 32em;
    background-color: rgba(255, 255, 255, 0.55);
    box-shadow: 0px 16px 40px rgba(112, 144, 176, 0.15);
    border-radius: 50px;
    
    font-size: 1.10625em;
    font-weight: 400;
    box-sizing: border-box;
    padding: 1.875em 1.8em;
    margin-top: 2%;
}
#devskill {
    margin-left: 10%;
    
}

#etcskill {
    margin-left: 52%;
}

#softskill {
    margin-left: 52%;
    margin-top: 31em;
}

#skillinst {
    font-size: 0.46em;
    font-weight: 400;
    padding-left: 0.625em;
}

.skillName {
    font-weight: 700;
    font-size: 1.1em;
    margin-left: 0.625em;
    vertical-align:super;
}

.skillName2 {
    font-weight: 600;
    font-size: 1em;
}

#Low {
    color: #DF1515;
}

#Middle {
    color: #FFAC0C;
}

#High {
    color: #236CDA;
}

#icon {
    margin-top: 1em;
}

#level {
    position: absolute;
    right: 0;
    margin-right: 2em;
}
