@font-face {
    font-family: 'Pretendard';
    font-style: normal;
    font-display: swap;
}

/* 각각의 글꼴 두께에 대한 정의 */
@font-face {
    font-family: 'Pretendard';
    font-weight: 100;
    src: url('./asset/font/Pretendard-Thin.ttf') format("truetype");
}

@font-face {
    font-family: 'Pretendard';
    font-weight: 200;
    src: url('./asset/font/Pretendard-ExtraLight.ttf') format("truetype");
}

@font-face {
    font-family: 'Pretendard';
    font-weight: 300;
    src: url('./asset/font/Pretendard-Light.ttf') format("truetype");
}

@font-face {
    font-family: 'Pretendard';
    font-weight: 400;
    src: url('./asset/font/Pretendard-Regular.ttf') format("truetype");
}

@font-face {
    font-family: 'Pretendard';
    font-weight: 500;
    src: url('./asset/font/Pretendard-Medium.ttf') format("truetype");
}

@font-face {
    font-family: 'Pretendard';
    font-weight: 600;
    src: url('./asset/font/Pretendard-SemiBold.ttf') format("truetype");
}

@font-face {
    font-family: 'Pretendard';
    font-weight: 700;
    src: url('./asset/font/Pretendard-Bold.ttf') format("truetype");
}

@font-face {
    font-family: 'Pretendard';
    font-weight: 800;
    src: url('./asset/font/Pretendard-ExtraBold.ttf') format("truetype");
}

@font-face {
    font-family: 'Pretendard';
    font-weight: 900;
    src: url('./asset/font/Pretendard-Black.ttf') format("truetype");
}

/* 색상 정의 */
#keycolor,
#keycolorSemibold {
    color: #5041FF;
}

/* Semibold 스타일 정의 */
#keycolorSemibold {
    font-weight: 600;
}

/* 배경 스타일 정의 */
#back {
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: -1;
    background: 
        linear-gradient(#685bfb, transparent), 
        linear-gradient(-55deg, #de8cf5, transparent), 
        linear-gradient(55deg, #77faf1, transparent); 
}

#back2 {
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 10;
    background: rgba(0, 0, 0, 0.8);  
}

html, body { 
    margin: 0;
    padding: 0;
    bottom: 0;
    box-sizing: border-box;
    font-family: 'Pretendard';
}
