#nav {
    position: absolute;
    display: flex;
    justify-content: space-evenly;
    flex-direction: column;
    height: 85%;
    margin-top: 1%;
    z-index: 0;
}

#navbox,
#asidebox {
    justify-content: center;
    background-color: rgba(255, 255, 255, 0.75);
    box-shadow: 0.0625em 0em 2.5em rgba(112, 144, 176, 0.15);
    transition: width 250ms;
}

#navbox {
    margin-top: 5%;
    left: 0;
    border-top-right-radius: 30px;
    border-bottom-right-radius: 30px;
    padding: 2.45em 0.8em 2em 0.3em;
}

#navbox:hover {
    width: 100%;
    padding: 2.45em 0.8em 2em 0.3em;
    background-color: transparent;
    box-shadow: none;
}

#navbox #icon,
#asidebox #icon {
    fill: #000000;
}

#navbox:hover #icon,
#asidebox:hover #icon {
    fill: #ffffff;
}

#aside {
    position: absolute;
    display: flex;
    right: 0;
    justify-content: space-between;
    flex-direction: column;
    margin-top: 5%;
    height: 28%;
    z-index: 0;
}

#asidebox {
    align-items: center;
    right: 0;
    padding: 1.2em 0.5em 1em 1em;
    background-color: rgba(255, 255, 255, 0.75);
    box-shadow: -0.0625em 0px 2.5em rgba(112, 144, 176, 0.15);
    border-top-left-radius: 50px;
    border-bottom-left-radius: 50px;
}

#asidebox:hover {
    background-color: transparent;
    box-shadow: none;
}

#profilepic {
    width: 26%;
    height: 26%;
}
